.footer {
  margin-top: 60px;

  text-align: center;
  background: #f7f7f7;
}

.text-footer {
  padding: 30px 0;

  font-weight: 400;
  font-size: 16px;
  line-height: calc(19 / 16);

  color: #545454;
  & a {
    text-decoration: underline;
    &:hover {
      color: var(--color-accent);
    }
  }
}
